import { createWebHistory, createRouter } from "vue-router";

import MainRoot from './components/mainRoot.vue'
import Product from './components/mainProduct.vue'
import Sales from './components/mainSales.vue'
import Service from './components/mainService.vue'

const routes = [
    {
        path        : '/',
        component   : MainRoot
    },
    {
        path        : '/product',
        component   : Product
    },
    {
        path        : '/sales',
        component   : Sales
    },
    {
        path        : '/service',
        component   : Service
    },
]

const router = createRouter({
    history : createWebHistory(), routes
});

export default router