<template>
    <div id="ModelContainer">

        <h2>
            Model
        </h2>
        <div class="showModel_wrap">
            <nav>
                <a href="" v-for="(item, i) in modelCat" :key='i' @click.prevent="catChg(i, item.title)" v-bind:class="{On:i===crt}">
                    {{item.title}}
                </a>
            </nav>
            <div class="in_wrap">
                <div class="content" v-for="(Models, i) in modelShow" :key="i" >
                    <div v-if="ctg==Models.Cat">
                        <h3 @click="openDetail(Models.id)">{{Models.catDet}}</h3>
                        <img :src=Models.image :alt="Models.id" @click="openDetail(Models.id)">


                    </div>
                    <detail v-if="modal" :modal="modal" :bike="modelShow[det]" :det="det" @closeModal="closeDetail"/>
                    <!--
                    <div v-if="ctg=='ALL'" style="max-">
                        <h3 @click="openDetail(i)">{{Models.catDet}}</h3>
                        <img :src=Models.image :alt="Models.id" @click="openDetail(i)">
                    </div>
                    -->
                </div>
                
            </div>
        </div>

    </div>
</template>
<script>
import modelCat from '../assets/modelCat.js'
import modelShow from '../assets/modelShow.js'
import detail from './ModalWrap.vue'
export default {
    data() {
        return {
            modelCat  : modelCat,
            modelShow : modelShow,
            modal     : false,
            crt       : 0,
            ctg       : "CUB",
            det       : 0
        }
    },
    methods: {
        catChg(i, item){
            this.crt = i
            this.ctg = item
        },
        openDetail(i){
            this.det = i
            this.modal = true
            console.log('i :'+ i);
            console.log('det :'+this.det);
        },
        closeDetail(i){
            this.det = i
            this.modal = false
        },
    },
    components: {
        detail
    }
    
}
</script>
<style scoped>
    #ModelContainer {
        margin-bottom: 10rem
    }
    .showModel_wrap {
        max-width: 100%;
    }
    .showModel_wrap nav {
        display: flex;
        justify-content: space-around;
        height: 5rem;
        align-items: center;
    }
    .showModel_wrap nav a {
        display: block;
        height: 100%;
        width: 100%;
        line-height: 5rem;
        border-bottom: 3px solid rgb(224, 224, 224)
    }
    .showModel_wrap nav a.On {
        background-color: rgb(244, 233, 255);
        color: rgb(97, 47, 143);
        border-bottom: 3px solid rgb(97, 47, 143)
    }
    .showModel_wrap>div {
        height: 30rem;
        border: 1px solid rgb(224, 224, 224);
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 5rem;
    }
    .showModel_wrap>div p {
        font-weight: 600;
        font-size: 3rem;
    }
    .svc_btn {
        border: none;
        padding: .5rem 2rem;
        border-radius: 3rem;
        cursor: pointer;
    }
    .in_wrap {
        justify-content: space-around;
        display: flex;
        flex-wrap: wrap;
        cursor: pointer;
        align-items: center;
    }

    img {
        /* width: auto; */
        height: auto;
    }
    h3 {
        color: gray;
        font-size: 1.6rem;
        margin: 3rem 0 1rem;
        position: relative
    }
    h3:after {
        display: block;
        content: "";
        width: 10%;
        background-color: gainsboro;
        margin: 1rem auto
    }
    .more {
        border: none;
        color: rgb(77, 77, 77);
        border-radius: 2rem;
        padding: 0 1rem;
        display: inline-block;
        margin-top: 3rem;
        line-height: 3rem;
        cursor: pointer;
    }
</style>